.fade-bottom {
  animation: fade-bottom 1.25s;
  animation-fill-mode: both;
  /* animation-delay: 0s; */
}

@keyframes fade-bottom {
  0% {
    opacity: 0.85;
    transform: scale(0.25) translateY(50vh);
  }
  40% {
    transform: scale(1.01) translateY(-5.5vh);
  }
  95% {
    opacity: 0.98;
    transform: scale(1) translateY(0vh);
  }
  100% {
    opacity: 1;
    transform: scale(1.0025);
  }
}
