.fade-bottom {
  -webkit-animation: fade-bottom 1.25s;
          animation: fade-bottom 1.25s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  /* animation-delay: 0s; */
}

@-webkit-keyframes fade-bottom {
  0% {
    opacity: 0.85;
    -webkit-transform: scale(0.25) translateY(50vh);
            transform: scale(0.25) translateY(50vh);
  }
  40% {
    -webkit-transform: scale(1.01) translateY(-5.5vh);
            transform: scale(1.01) translateY(-5.5vh);
  }
  95% {
    opacity: 0.98;
    -webkit-transform: scale(1) translateY(0vh);
            transform: scale(1) translateY(0vh);
  }
  100% {
    opacity: 1;
    -webkit-transform: scale(1.0025);
            transform: scale(1.0025);
  }
}

@keyframes fade-bottom {
  0% {
    opacity: 0.85;
    -webkit-transform: scale(0.25) translateY(50vh);
            transform: scale(0.25) translateY(50vh);
  }
  40% {
    -webkit-transform: scale(1.01) translateY(-5.5vh);
            transform: scale(1.01) translateY(-5.5vh);
  }
  95% {
    opacity: 0.98;
    -webkit-transform: scale(1) translateY(0vh);
            transform: scale(1) translateY(0vh);
  }
  100% {
    opacity: 1;
    -webkit-transform: scale(1.0025);
            transform: scale(1.0025);
  }
}

